import React, { useEffect, useState } from 'react';
import {
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Backdrop,
  Button,
  CircularProgress,
  Divider,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemText,
  Slider,
  Table,
  TableBody,
  TableContainer,
  TableHead,
  TableRow,
  TableCell,
  Theme,
  Typography,
} from '@material-ui/core';

import { makeStyles } from '@material-ui/core/styles';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';

import moment from 'moment';
import { formatBytes } from '../../../Utility/helper';

const useStyles = makeStyles((theme: Theme) => ({
  drawerContainer: {
    width: '100%',
  },
  header: {
    display: 'flex',
    alignItems: 'center',
  },
  item: {
    height: '100px',
    padding: '0 22px',
  },
  summary: {
    height: '56px',
    cursor: 'pointer',
    '&$selected, &$selected:hover': {
      backgroundColor: theme.palette.secondary.main,
    },
  },
  selected: {},
  hover: {},
  text: {
    fontSize: '1.5em',
    fontWeight: 'bold',
  },
}));

const FileManager = ({ drawerOpen, setDrawerOpen, fileLocation }) => {
  const classes = useStyles();
  const [selected, setSelected] = useState('');
  const [loading, setLoading] = useState(true);
  const [files, setFiles] = useState([]);

  useEffect(() => {
    if (drawerOpen) {
      setLoading(true);
      //make api call to fetch files
      //use fileLocation (usb or local) to determine which call to make
      //TODO:REMOVE TIMEOUT
      async function getFiles() {
        // Default options are marked with *
        let response = null;

        if(fileLocation == 'local') {
          response = await fetch(`http://makerhub.local:8080/api/v1/files`);
        } else {
          await fetch('http://makerhub.local:8080/api/v1/usb/refresh', {method: "POST"});
          response = await fetch(`http://makerhub.local:8080/api/v1/usb/files`);
        }
        return response.json(); // parses JSON response into native JavaScript objects
      }

      getFiles().then(data => {
        console.log(data);
        setFiles(data.files);
        setLoading(false);
      });
    }
  }, [drawerOpen]);

  const handleSelect = () => {
    console.log(selected);
    //call api to print file
    fetch(`http://makerhub.local:8080/api/v1/job/${selected}`, {
      method: "POST",
      headers: { "Content-Type": "application/json" }
    });
    setDrawerOpen(() => false);
    //call function or swap to status screen
    setSelected('');
  };

  return (
    <Drawer
      anchor="right"
      open={drawerOpen}
      onClose={() => setDrawerOpen(false)}
      classes={{
        paper: classes.drawerContainer,
      }}
    >
      <div className={classes.header}>
        <IconButton aria-label="back" onClick={() => setDrawerOpen(false)}>
          <ArrowBackIcon />
        </IconButton>
        <h2>{fileLocation.toUpperCase()}</h2>
        <Button
          variant="contained"
          disabled={selected.length <= 0}
          style={{ marginLeft: 'auto', marginRight: '13px' }}
          onClick={() => handleSelect()}
        >
          Print
        </Button>
      </div>
      {loading ? (
        <Backdrop className={classes.backdrop} open={true}>
          <CircularProgress color="inherit" />
        </Backdrop>
      ) : (
        <TableContainer>
          <Table>
            <TableHead>
              <TableRow>
                <TableCell>Name</TableCell>
                <TableCell>Date</TableCell>
                <TableCell>Size</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {files && files.map((file) => {
                return (
                  <TableRow
                    key={file.display}
                    onClick={() => setSelected(file.display)}
                    selected={selected === (file.display)}
                    className={classes.summary}
                    classes={{
                      selected: classes.selected,
                      hover: classes.hover,
                    }}
                  >
                      <TableCell>{file.display}</TableCell>
                      <TableCell>
                        {moment(file.date).format('MM/DD/YYYY')}
                      </TableCell>
                      <TableCell>{formatBytes(file.size)}</TableCell>
                  </TableRow>
                );
              })}
            </TableBody>
          </Table>
        </TableContainer>
      )}
    </Drawer>
  );
};

export default FileManager;
