import React from 'react';
import { Container } from '@material-ui/core';
import { Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles({
  buttonContainer: {
    display: 'flex',
    justifyContent: 'center',
    marginTop: '10px',
  },
});

const PrintComplete = ({ setStatus, progressState, jobState, setActiveMode }) => {
  const classes = useStyles();
  return (
    <>
    <Container className={classes.buttonContainer}>
      <Button
        color="primary"
        variant="contained"
        onClick={() => {
          fetch(`http://makerhub.local:8080/api/v1/job/${jobState.file.name}`, {
            method: "POST",
            headers: { "Content-Type": "application/json" }
          });
        }}
        size="large"
      >
        Print Again
      </Button>
      </Container>
      <Container className={classes.buttonContainer}>
      <Button
        color="secondary"
        variant="contained"
        size="large"
        onClick={() => setStatus(0)}
        style={{ marginTop: '12px' }}
      >
        Exit
      </Button>
      </Container>
    </>
  );
};

export default PrintComplete;
