import React, { useState, useEffect } from 'react';
import { Container } from '@material-ui/core';
import { Button, LinearProgress } from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPause, faPlay, faStop } from '@fortawesome/free-solid-svg-icons';

import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles({
  buttonContainer: {
    display: 'flex',
    justifyContent: 'center',
    marginTop: '10px',
  },
    textCenter: {
      textAlign: 'center',
      marginTop: '10px',
    },
});

const PrintStatus = ({ lastMessage, progressState, setDisabledNav, state }) => {
  const classes = useStyles();
  const [paused, setPaused] = useState(false);
  const [value, setValue] = useState(0);

  let sendGcode = function(gcode) {
    fetch(`http://makerhub.local:8080/api/v1/send`, {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        gcode: gcode
      })
    });
  }


  useEffect(() => {
    if(progressState.completion !== value) {
      setValue(progressState.completion);
      console.log(value);
    }
  }, [progressState]);

  return (
    <>
      <Container>
      <div className="print-btn-container">
        <Button
          color="primary"
          variant="contained"
          onClick={() => {
            setPaused((prevState) => !prevState);
            fetch(`http://makerhub.local:8080/api/v1/job`, {
              method: "PATCH",
              headers: { "Content-Type": "application/json" },
              body: JSON.stringify({
                file: state.files[state.file]
              })
            });
          }}
        >
          {!paused ? (
            <span>
              <FontAwesomeIcon icon={faPause} /> Pause
            </span>
          ) : (
            <span>
              <FontAwesomeIcon icon={faPlay} /> Play
            </span>
          )}
        </Button>
        <Button
          color="secondary"
          variant="contained"
          onClick={() => {
            fetch(`http://makerhub.local:8080/api/v1/job`, {
              method: "DELETE",
              headers: { "Content-Type": "application/json" },
              body: JSON.stringify({
                file: state.files[state.file]
              })
            });
          }}
        >
          <span>
            <FontAwesomeIcon icon={faStop} /> Stop
          </span>
        </Button>
      </div>
      <div className="print-status-bar">
        <LinearProgress variant="determinate" value={value} />
      </div>
      </Container>
    </>
  );
};

export default PrintStatus;
